</tr>
<tr>
<td><span class="prop-key" style="white-space:nowrap;">disable-past</span></td>
<td><span class="type type-boolean">Boolean</span></td>
<td>false</td>
<td><span style="font-size:12px;white-space:nowrap;"></span></td>
<td>禁止选择过去的日期，该选项可以与start-date同时使用</td>
</tr>
<tr>
<td><span class="prop-key" style="white-space:nowrap;">disable-future</span></td>
<td><span class="type type-boolean">Boolean</span></td>
<td>false</td>
<td><span style="font-size:12px;white-space:nowrap;"></span></td>
