</p>
<p>非联动情况下，列表数据格式示例：</p>
<pre class="hljs"><code><span class="hljs-comment">// data</span>
[[<span class="hljs-string">'小米'</span>, <span class="hljs-string">'iPhone'</span>, <span class="hljs-string">'华为'</span>, <span class="hljs-string">'情怀'</span>, <span class="hljs-string">'三星'</span>, <span class="hljs-string">'其他'</span>, <span class="hljs-string">'不告诉你'</span>], [<span class="hljs-string">'小米1'</span>, <span class="hljs-string">'iPhone2'</span>, <span class="hljs-string">'华为3'</span>, <span class="hljs-string">'情怀4'</span>, <span class="hljs-string">'三星5'</span>, <span class="hljs-string">'其他6'</span>, <span class="hljs-string">'不告诉你7'</span>]]

<span class="hljs-comment">// 或者使用 name =&gt; value 的形式</span>
[[{
  <span class="hljs-attr">name</span>: <span class="hljs-string">'2019届5班'</span>,
  <span class="hljs-attr">value</span>: <span class="hljs-string">'1'</span>
}, {
  <span class="hljs-attr">name</span>: <span class="hljs-string">'2019届4班'</span>,
  <span class="hljs-attr">value</span>: <span class="hljs-string">'2'</span>
}]]

<span class="hljs-comment">// value</span>
[<span class="hljs-string">'小米'</span>, <span class="hljs-string">'小米1'</span>]
</code></pre>
