<td>false</td>
<td><span style="font-size:12px;white-space:nowrap;"></span></td>
<td>指定初始化时绑定的数据是否为文本(即省市名，而不是id)类型，即对于非id值组件内部会转换为id。如果是异步设置value，只能使用id赋值。</td>
</tr>
<tr>
<td><span class="prop-key" style="white-space:nowrap;">title</span></td>
